import React, { ChangeEvent } from "react"
import { AiOutlineSearch } from "react-icons/ai"
import lodash from "lodash"
const SearchBar = ({ setData }: { setData: (v: any) => void }) => {
  const debounceOnChange = lodash.debounce(
    (e: ChangeEvent<HTMLInputElement>) => {
      setData && setData(e.target.value)
    },
    450
  )
  return (
    <div className="flex flex-row h-full items-center w-[30rem]">
      <AiOutlineSearch className="absolute pl-2 text-black" size={"2rem"} />
      <input
        placeholder=" "
        className="outline-none w-full h-full pl-12 rounded-xl placeholder:text-Grayscale/40 text-lg border-[1px] border-Grayscale/20 bg-white"
        onChange={debounceOnChange}
      />
    </div>
  )
}
export default SearchBar
