import { IPostItem } from "src/types/PostItem.type"
import React, { useEffect, useState } from "react"
import Tag from "./post/Tag"
import HorizontalScroll from "react-scroll-horizontal"
export const Filter = ({
  postList,
  setData,
}: {
  postList: IPostItem[]
  setData: (v: string) => void
}) => {
  const [tagList, setTagList] = useState<string[]>([])
  const [activeIdx, setActiveIdx] = useState<number>(0)
  useEffect(() => {
    const data = postList.flatMap(
      ({
        node: {
          frontmatter: { tags },
        },
      }) => tags || []
    )
    const tagSet = new Set(data)
    setTagList(["", ...tagSet])
  }, [postList])

  const TagList = tagList.map((tag, idx) => {
    return (
      <div
        onClick={() => {
          setActiveIdx(idx)
          setData(tag)
        }}
        className="cursor-pointer"
      >
        <Tag tag={tag} key={idx} active={idx === activeIdx} />
      </div>
    )
  })
  return (
    <div className="h-full w-[40rem] border-l-8 border-Greenlight/10 flex justify-center overflow-auto scroll-hidden bg-bg-color2 duration-300">
      <HorizontalScroll className="w-full h-full" reverseScroll={true}>
        <div className="h-full w-[95%] flex justify-between items-center ">
          {TagList}
        </div>
      </HorizontalScroll>
    </div>
  )
}

export default Filter
